<template>
  <div id="app" class="charContent" v-show="isChatLoaded">
    <transition name="fade">
    <div id="chatBox" v-show="showChatBox">
      <div id="chatLine" class="chatLine" v-for="(buffer, i) of messageArray" :key="buffer.message">
        <!--NORMAL PROXIMITY-->
        <div v-if="isAdminMode">
          <h3 :id="'chatText:' + i" v-bind:class="getTextColor(i)">{{buffer.id}} {{buffer.date}} <span v-html="buffer.message">{{buffer.message}}</span></h3>
        </div>
        <div v-else>
          <h3 :id="'chatText:' + i" v-bind:class="getTextColor(i)">{{buffer.date}} <span v-html="buffer.message">{{buffer.message}}</span></h3>
        </div>
      </div>
    </div>
    </transition>
    <transition name="fade">
    <div class="chatInput" ref="input" v-show="showChatInput">
      <input type="text" id="inputTextID" class="inputText" v-model="inputText">
    </div>
    </transition>
    <transition name="fade">
    <div class="chatHelperContainer" id="helperScroll" v-show="showChatHelper">
      <div class="chatHelperText" :id="'help_row:' + i" v-for="(helper, i) of filterHelpMsg" :key="helper.cmd">
        <h3 class="helpText">{{helper.cmd}}</h3>
        <h3 class="helpText">{{helper.args}}</h3><br>
        <h3 class="helpText">{{helper.description}}</h3><br>
      </div>
    </div>
    </transition>
  </div>
</template>

<script>
export default {
  name: 'chatBox',
  methods: {

    getTextColor: function(i) {
      setTimeout(() => {
          const color = 'rgba(' + this.messageArray[i].r + ',' + this.messageArray[i].g + ',' + this.messageArray[i].b + ',' + this.messageArray[i].a + ')'
          document.getElementById('chatText:' + i).style.opacity = 1;
          document.getElementById('chatText:' + i).style.display = 'block';
          document.getElementById('chatText:' + i).style.color = color;
          document.getElementById('chatText:' + i).style.width = '100%';
        }, 0);
    },

    buttonClick: function () {
      this.showChatInput = !this.showChatInput
      this.showChatHelper = !this.showChatHelper
    },

    checkScrollView: function () {
      let chatElement;
      if(this.showChatBox) {
        chatElement = document.getElementById("chatBox")
      }
      let maxScrollPosition = chatElement.scrollHeight - chatElement.clientHeight;
      // example
      chatElement.scrollTop = maxScrollPosition;
    },

    keyPressed: function (key) {
      let chatElement;
      let elmnt;
      //Gets helper components if open
      if(this.showChatBox) {
        chatElement = document.getElementById("chatBox")
      }
      if(this.showChatHelper) {
        elmnt = document.getElementById("helperScroll");
      }

      if(key === 'Enter' && this.inputText.length >= 0) {
        let getObjectIndex = this.registedCmd.map(function(item) { return item.cmd; }).indexOf(this.inputCmd);
        if(this.inputText === undefined || this.inputText.length === 0) {
          this.setInputState(false);
          alt.emit('chat:hideChat')
          return;
        } else if (this.inputText.charAt(0) === '/' && !(/\s/.test(this.inputText)) && this.showChatHelper && this.helpOption !== null) {
            this.inputText = this.registedCmd[this.helpOption].cmd;
            this.helpOption = null;
            return;
        } 
        if(this.inputText.charAt(0) === '/' && this.inputText.length > 0) {
          //if(getObjectIndex === -1) {return;}
          const firstSpace = this.inputText.indexOf(" ");
          const newStr = this.inputText.slice(firstSpace);
          alt.emit('chat:sendcommand', this.inputCmd, newStr);
        } else {
          if(!this.showChatHelper) {
            alt.emit('chat:sendmessage', this.inputText);
          }
        }
        //Set input text to chat history
        this.historyChat.push(this.inputText);
        //sets history length
        this.currentHistoryPosition = this.historyChat.length
        //check scroll height;
        this.showChatBox = true; // <- CAREFULL
        this.checkScrollView();
        this.setInputState(false);
        alt.emit('chat:hideChat')
        if(this.historyChat.length > 20) {
          //removes the first field after reaching maximum length
          this.historyChat.shift();
        }

      } else if(key === 'ArrowUp') {
        //if has the chat helper open
        if(this.inputText) {
          if(this.showChatHelper) {
            if(this.helpOption == null) {
              this.helpOption = 0;
            }
            document.getElementById("help_row:" + this.helpOption).style.backgroundColor = 'transparent'
            document.getElementById("help_row:" + this.helpOption).style.borderRadius = '0px'
            if(this.helpOption > 0) {
              this.helpOption -= 1
            }
            elmnt.scrollTop -= 40;
            document.getElementById("help_row:" + this.helpOption).style.width = '80%'
            document.getElementById("help_row:" + this.helpOption).style.backgroundColor = '#32b85f9f'
            document.getElementById("help_row:" + this.helpOption).style.borderRadius = '15px'
            document.getElementById("help_row:" + this.helpOption).style.transition = '.4s'
          } else {
            
            this.currentHistoryPosition = this.currentHistoryPosition + 1;
            this.inputText = this.historyChat[this.currentHistoryPosition];
            //limitator
            if(this.currentHistoryPosition > this.historyChat.length) {
              this.currentHistoryPosition = this.historyChat.length
            }
            //currentHistoryPosition
          }
        }
      } else if (key === 'ArrowDown') {
        if(this.inputText !== undefined) {
          if(this.showChatHelper) {
            if(this.helpOption == null) {
              this.helpOption = 0;
            }
            document.getElementById("help_row:" + this.helpOption).style.backgroundColor = 'transparent'
            document.getElementById("help_row:" + this.helpOption).style.borderRadius = '0px'
            if(this.helpOption < this.filterHelpMsg.length - 1) {
              
              this.helpOption += 1
            }
            elmnt.scrollTop += 40;
            document.getElementById("help_row:" + this.helpOption).style.width = '80%'
            document.getElementById("help_row:" + this.helpOption).style.backgroundColor = '#32b85f9f'
            document.getElementById("help_row:" + this.helpOption).style.borderRadius = '15px'
            document.getElementById("help_row:" + this.helpOption).style.transition = '1s'
          } else {
            //limitator

            this.currentHistoryPosition = this.currentHistoryPosition - 1;
            this.inputText = this.historyChat[this.currentHistoryPosition];

            if(this.currentHistoryPosition < 0) {
              this.currentHistoryPosition = 0
            }
          } 
        }
      } else if (key === 'PageUp') {
        if(this.showChatBox) {
          chatElement.scrollTop -= 40
        }
      } else if (key === 'PageDown') {
        if(this.showChatBox) {
          chatElement.scrollTop += 40
        }
      }

      //Limiters
      if(this.helpOption <= 0) {this.helpOption = 0}
    },
    
    loadChat: function (permissions, availableCommands) {
      //CHAT LOADER
      this.currentPermission = permissions;
      this.registedCmd = availableCommands;
      this.isChatLoaded = true
      this.isMounted = true
    },

    //Set input box state
    setInputState: function (state) {
      this.inputText = '';
      this.showChatInput = state;
      if(!state) {this.showChatHelper = false;}
      setTimeout(() => {
        if(state) {
          this.showChatBox = true;
          document.getElementById("inputTextID").focus()
        }
      }, 10);
    },

    //Display message to all players
    displayMessage: function (obj) {
      this.messageArray.push(obj)
    },

    toggleAdmin: function (state) {
      this.isAdminMode = state;
    },

    setPermission: function (permissionLevel, availableCommands) {
      this.currentPermission = permissionLevel;
      this.registedCmd = availableCommands;
    },

    disapearDelay: function (index) {
      const i = index.length;
      this.showChatBox = true
      if(this.messageTimeout) {
        clearTimeout(this.messageTimeout)
      }
      this.messageTimeout = setTimeout(() => {
          this.showChatBox = false
          clearTimeout(this.messageTimeout)
          //clear own timeOut after timeout time
      }, 10000);  
    }
  },
  data() {
    return {
      currentHistoryPosition: 0,

      isAdminMode: false,
      isMounted: false,
      showChatInput: false,
      showChatBox: false,
      showChatHelper: false,

      currentPermission: 0,
      
      historyChat: [],
      messageArray: [],
      registedCmd: [],
      helperMsgs: [],

      inputText: '',
      inputCmd: '',
      helpOption: null,

      messageTimeout: null,

      isChatLoaded: false
    }
  },
  watch: {
    'showChatInput': function (state) {
        if(state) {
          this.showChatBox = true
        }
    },
    'messageArray': function (newRow, index) {
        if(newRow) {
          this.disapearDelay(index);
        }
        
    }
  },
  computed: {
    filterHelpMsg: function() {
      return this.registedCmd.filter((cmd) => {      
        //Detects if there is a / before tx
        if(this.inputText.length !== 0) {
          this.helpOption = null
          if(this.inputText.charAt(0) === '/') {
            this.inputCmd = this.inputText.split(" ")[0];
            if(this.inputCmd === this.inputText) {
              this.showChatHelper = true
            } else {
              this.showChatHelper = false
            }
          } else {
            if(this.showChatHelper) {
              this.showChatHelper = false
            }
          }
          this.helperMsgs = cmd
          //Player only see commands if he has enought permission
          if(this.currentPermission >= cmd.level) {
            return cmd.cmd.match(this.inputCmd);
          }
        }
      });
    }
  },
  mounted() {
    if('alt' in window) {
      setTimeout(() => {
        alt.emit('chat:ready')
      }, 200);

      //event listeners
      alt.on('chat:loadChat', this.loadChat);
      alt.on('chat:input_state', this.setInputState);
      alt.on('chat:keyPress', this.keyPressed);
      alt.on('chat:sendProximity', this.displayMessage);
      alt.on('chat:setPermission', this.setPermission);
      alt.on('chat:toggleAdmin', this.toggleAdmin);
    }
  },
}
</script>
<style scoped>
