  user-select: none;
  position: absolute;
  top: 32.7%;
  z-index: -2;
  max-height: 20%;
  width: 30%;
  left: 1%;
  background-color: rgba(0, 0, 0, 0.411); 
  border-radius: 0px 0px 10px 10px;
  overflow-x: hidden;
  overflow-y: auto;
}

