  background-color: transparent;
  z-index: -2;
}

.chatHelperContainer::-webkit-scrollbar-button {
  background-color: transparent;
  z-index: -2;
}

.chatHelperContainer::-webkit-scrollbar {
  background-color: transparent;
  z-index: -2;
}

.chatHelperText {
  position: relative;
  padding: 1%;
  background-color: transparent;
  height: auto;
  width: 80%;
}

.chatLine {
  width: 100%;
  height: auto;
}

