  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: .7vw;
  margin: 0%;
  float: left;
  text-align: left;
  color: rgb(255, 255, 255);
  text-shadow: rgb(0, 0, 0) 0px 0px 2px, rgb(0, 0, 0) 1.75517px 0px 0px, rgb(0, 0, 0) 1.0806px 0px 0px, rgb(0, 0, 0) 0.141474px .5px 0px, rgb(0, 0, 0) -0.832294px 0px 10px, rgb(0, 0, 0) -1.60229px 1px 2px, rgb(0, 0, 0) -1.17998px 0.28224px 0px, rgb(0, 0, 0) -1.27291px -0.701566px 0px, rgb(0, 0, 0) -1.10729px -1.2136px 0px, rgb(0, 0, 0) -0.421592px -1.25506px 0px, rgb(0, 0, 0) 0.567324px -1.71785px 0px, rgb(0, 0, 0) 1.41734px -1.21108px 0px, rgb(0, 0, 0) 1.2034px -0.158831px 0px;
}


#chatBox {
  position: absolute;
  height: 25%;
  width: 30%;
  left: 1%;
  top: 2%;
  background-color: rgba(0, 0, 0, 0);
  overflow: hidden;
}

.chatInput {
  position: absolute;
  border-radius: 5px;
  height: 5%;
