}

.inputText {
  position: relative;
  font-size: .9vw;
  font-family: Arial, Helvetica, sans-serif;
  padding-left: 2%;
  color: white;
  background-color: transparent;
  border: 1px solid black;
  border-radius: 5px;
  outline: 0px;
  left: 0%;
